@media (max-width: 768px) {
   .warehouse__slide img {
		min-width: 220px;
		min-height: 160px;
	}

 	.service-steps__item {
   	flex-basis: 30%;
 	}

 	.service-maintenance__items {
 		margin-top: 40px;
 		flex-wrap: wrap;
 		justify-content: space-around;
 	}

 	.service-maintenance__item {
 		margin-top: 42px;
 		flex: 0 0 45%;
 	}
}
  
@media (max-width: 620px) {
	.container {
		max-width: 500px;
	}

	.advantages__title {
   	font-size: 26px;
   	line-height: 30px;
   }

   .advantages__sidebar {
   	max-width: 350px;
   	padding: 26px 40px 30px 30px;
 	}

 	.advantages__item img {
 		width: 25px;
 		height: 25px;
 	}

 	.price__form {
 		justify-content: flex-end;
 	}

 	.price__form-label:not(:first-child) {
 		margin-top: 25px;
 	}

 	.price__form-btn {
 		margin-top: 30px;
 		margin-left: 0;
 	}

 	.price__form-btn {
 		padding: 20px 60px;
 	}

 	.price__form-text {
 		margin-right: 0;
 		margin-left: auto;
 	}

 	.delivery__items {
   	margin-top: 25px;
 	}

 	.search__form-input:not(:last-child) {
 		margin-bottom: 24px;
 		margin-right: 0;
 	}

 	.search__form-btn {
	   margin-top: 10px;
	}

	.search__form-label span {
	   right: 25px;
	}

	.search__description-text:not(:first-child) {
	   margin: 14px auto 0 auto;
	}

	.search__description-phone-number {
	   margin-top: 12px;
	}

	.free-consultation__form {
	   min-width: 500px;
	}

	.service-steps__items {
		margin-top: 40px;
		flex-wrap: wrap;
		justify-content: space-around;
	}

	.service-steps__item {
		margin-top: 40px;
	   flex-basis: 100%;
	}

	.service-steps__item-text {
		max-width: 240px;
	}

	.warehouse__subtitle {
		max-width: 420px;
	}

	.service-maintenance__item {
		flex: 0 0 100%;
	}

	.service-maintenance__item-text {
		max-width: 60%;
	}

	.free-consultation__form {
		min-width: 260px;
		justify-content: center;
	}

	.free-consultation__form-btn {
		margin-top: 24px;
	}

	.free-consultation__phone-number {
		margin-top: 6px;
	}

	.office__text-phone-number {
   	margin-top: 16px;
   }

	.footer__text-column {
		display: none;
	}   

	.footer__text {
   	min-width: 30%;
   }
}

@media (max-width: 576px) {
	html, body {
		font-size: 6px;
	}

	.container {
		max-width: 400px;
	}

	.map iframe {
		height: 320px;
	}

	.advantages__sidebar {
   	max-width: 320px;
   	padding: 26px 20px 30px 30px;
   	margin: 0 auto;
	}

	.advantages__title {
   	font-size: 24px;
   	line-height: 28px;
	}

	.price__title {
		text-align: center;
	}

	.price__form {
		justify-content: center;
	}

	.price__form-label {
		margin-top: 20px;
	}

	.price__form-text {
		margin: 14px auto 0 auto;
	}

	.delivery__column {
		margin: 0;
	}

	.delivery__item {
		flex: 0 0 45%;
	}

	.search__form-input:not(:last-child) {
		margin-bottom: 20px;
	}

	.search__description-text,  .search__description-text:first-child{
		max-width: 80%;
		margin: 0 auto;
	}

	.search__description-text:not(:first-child) {
   	margin: 14px auto 0 auto;
	}

	.guarantee__item {
		flex: 0 0 100%;
	}

	.guarantee__item-text {
   	margin: 20px auto 0 auto;
   	max-width: 70%;
	}

	.guarantee__item-image {
   	height: 230px;
   	width: 190px;
 	}

 	.guarantee__item-more {
   	margin-top: 12px;
	}

	.questions__page-description {
   	margin-left: 2%;
	}

	.questions__list-item-link {
		font-size: 14px;
	}

	.service-steps__items {
   	margin-top: 25px;
 	}

 	.warehouse__subtitle {
   	max-width: 80%;
	}

	.service-maintenance__item-text {
   	margin: 16px auto 0 auto
 	}

 	.office__text {
   	margin-top: 40px;
	}
}


@media (max-width: 425px) {
	.container {
		max-width: 340px;
	}

	.logo {
		max-width: 160px;
	}

	.header__btn {
	   margin-left: 0;
	   padding: 16px 14px;
	}

	.advantages__title {
    	font-size: 22px;
   	line-height: 26px;
   	text-align: center;
	}

	.advantages__subtitle {
		text-align: center;
	}

	.price__form {
		margin-top: 36px;
	}

	.delivery__subtitle {
		max-width: 80%;
	}

	.delivery__item-image {
   	width: 74px;
   	height: 64px;
	}

	.delivery__item-text {
   	margin: 20px auto 0 auto;
	}

	.search__subtitle {
		max-width: 80%;
	}

	.search__form-input:not(:last-child) {
   	margin-bottom: 15px;
	}

	.search__form-input {
   	padding: 5px 20px 0 20px;
	}

	.search__description {
   	margin-top: 34px;
	}

	.price__form-label:not(:first-child) {
   	margin-top: 16px;
	}

	.guarantee__items {
   	margin-top: 20px;
 	}

 	.discounts__item {
 		flex: 0 0 100%;
 	}

 	.discounts__item-text {
   	margin: 18px auto 0 auto;
		max-width: 80%;
	}

	.questions__page {
   	margin-top: 44px;
	}

	.questions__page-description {
   	margin-left: 0;
	}

	.questions__list-item {
   	padding-left: 6px;
	}

	.questions__list-item-link {
		font-size: 15px;
	}

	.service-steps__subtitle {
   	margin: 10px auto 0 auto;
   	font-size: 14px;
	}

	.service-steps__items {
   	margin-top: 15px;
	}

	.service-steps__item-text {
   	max-width: 60%;
	}

	.service-maintenance__item-image-block {
   	height: 148px;
   	width: 148px;
	}

	.service-maintenance__items {
   	margin-top: 25px;
 	}

 	.service-maintenance__item-image-block {
 		border: 4px solid #ffbc3b;	
 	}
 	
}

@media (max-width: 360px) {
	html, body {
		font-size: 5px;
	}

	.container {
		max-width: 300px;
	}

	.logo {
   	width: 120px;
 	}

 	.header__btn {
 		padding: 14px 12px;
 	}

 	.advantages__subtitle {
   	min-width: 96%;
	}

	.price__form-desc {
		max-width: 96%;
	}

	.delivery__item {
		flex: 0 0 100%;
	}

	.delivery__item-text {
		max-width: 70%;
	}

	.guarantee__items {
   	margin-top: 8px;
	}

	.discounts__title {
   	max-width: 100%;
 	}

 	.discounts__items {
 		margin-top: 10px;
 	}

 	.service-steps__item-image {
   	width: 80px;
   	height: 80px;
	}

	.service-steps__item-text {
   	margin: 20px auto 0 auto;
	}

	.service-maintenance__items {
   	margin-top: 10px;
	}

	.service-maintenance__item-text {
   	max-width: 86%;
	}

	.free-consultation__column {
		margin: 0;
	}

	.free-consultation__form {
		margin-top: 34px;
	}
}
